img {
  width: 100%;
}

.landing {
  position: relative;
  background: url(/culture-portal/static/media/showcase.4b31330b.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}

#mapid {
  height: 380px;
}

nav {
  width:100%;
  padding: 5px;
  display: flex;
  justify-content: space-evenly;
  background: rgb(23,36,48);  
}

.nav-button {
  width: 200px;
  background: #007bff;
  border: #007bff solid;
  border-radius: .25rem;
  padding: 3px;
  list-style-type: none;
  cursor: pointer;
  text-align: center;
  font-size: 23px;
  color: whitesmoke;
}

.nav-button:hover {
  border: silver solid;
}
.main-page__div-background {
  background-image: url(/culture-portal/static/media/main-page-background.5d1a2dd4.jpg);
  background-size: 100% 100%;
  background-position: center center;
  min-height: calc(100vh - 48px);
  display: flex;
  justify-content: center;
}

.main-page__div-wrapper {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 70%;
  border-radius: 20px;
  z-index: 2;
  background: rgba(204, 204, 204, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 50px;
}

.main-page__div-container-title-artist-day {
  width: 90%;
  height: 100px;
  margin-top: 20px;
}

.main-page__badge-title-artist-day {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  height: 100px;
  display: flex;
  align-items: center;
}

.main-page__media-container-artist-day {
  width: 90%;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.main-page__div-container-image-artist {
  margin-right: 100px;
}

.main-page__media-image-artist {
  width: 300px;
  border-radius: 20px;
}

.main-page__div-about-artist {
  overflow: hidden;
}

.main-page__div-container-button-more {
  margin-top: 20px;
}

.main-page__div-container-about-portal {
  width: 90%;
  margin-top: 120px;
}

.main-page__div-container-team-project {
  width: 90%;
  margin-top: 120px;
}

.main-page__h5-title-team-ptoject {
  text-align: center;
}

.main-page__div-team-project {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.main-page__div-developer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-page__div-img-developer {
  width: 100px;
  height: 100px;
  background-size: 100% 100%;
  border-radius: 100%;
}

.main-page__div-img-first-developer {
  background-image: url(/culture-portal/static/media/developer-vladislav.8d46fa37.jpg);
}

.main-page__div-img-second-developer {
  background-image: url(/culture-portal/static/media/developer-anatolii.373a047d.jpg);
}

.main-page__div-img-third-developer {
  background-image: url(/culture-portal/static/media/developer-pavel.58097663.jpg);
}

.main-page__div-img-fourth-developer {
  background-image: url(/culture-portal/static/media/developer-artem.55c6f810.jpg);
}

.main-page__div-name-developer {
  margin-top: 20px;
  color: green;
}

@media screen and (max-width: 900px) {
  .main-page__div-wrapper {
    width: 90%;
  }

  .main-page__div-container-image-artist {
    margin-right: 15px;
  }

  .main-page__media-image-artist {
    width: 100px;
  }

  .main-page__div-team-project {
    flex-direction: column;
  }

  .main-page__div-developer {
    margin-bottom: 40px;
  }

  .main-page__div-last-developer {
    margin-bottom: 0px;
  }

  .main-page__div-img-developer {
    width: 120px;
    height: 120px;
  }
}

@media screen and (max-width: 520px) {
  .main-page__badge-title-artist-day {
    font-size: 25px;
  }

  .main-page__media-container-artist-day {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .main-page__div-container-image-artist {
    margin-right: 0px;
  }

  .main-page__badge-title-artist-day {
    justify-content: center;
  }

  .main-page__div-about-artist {
    margin-top: 50px;
  }

  .main-page__div-container-button-more {
    display: flex;
    justify-content: center;
  }

  .main-page__media-image-artist {
    width: 150px;
  }

  .main-page__div-container-about-portal {
    width: 90%;
    margin-top: 100px;
  }

  .main-page__h4-title-about-portal {
    display: flex;
    justify-content: center;
  }

  .main-page__div-information-about-portal {
    text-align: justify;
  }

  .main-page__div-about-artist {
    margin-top: 20px;
  }

  .main-page__div-container-name-artist {
    text-align: center;
  }
}

.list-page {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: calc(100vh - 48px);
  padding-top: 10px;
  background-image: url(/culture-portal/static/media/main-page-background.5d1a2dd4.jpg);
  background-size: cover;
  margin-bottom: 0;
}

.person-list-title {
  text-align: center;
  font-size: 2em;
  margin: 5px;
  color: whitesmoke;
}

.person-seacrh-container {
  display: flex;
  margin-top: 20px;
  align-items: center;
  justify-content: center;
}

.presearch {
  width:60px;
  font-weight: 700;
  margin-right: 5px;
  margin-bottom: 0;
  color: whitesmoke;
}

input {
  width: 200px;
}

.person-list-container {
  display: flex;
  flex-wrap: wrap;
  justify-content:space-evenly;
  background: rgba(204, 204, 204, 0.9);
  max-width: 70%;
  border-radius: 20px;
  margin: 50px auto;
}

.person-list-item {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  max-width: 200px;
  min-width: 200px;
  height: 160px;
  background: silver;
  border: yellowgreen solid;
  border-radius: 15px;
  margin: 10px 5px;
  cursor: pointer;
}
.person-list-item:hover {
  font-weight: 700;
  border: red solid;
}

.person-list-item > p {
  margin: 5px;
  text-align: center;
  font-size: 18px;
}
.person-list-item p:first-of-type {
  text-transform: uppercase;
}
.loc-control {
  padding: 3px;
  display: flex;
  width: 100%;
  justify-content: center;
  background: rgb(23,36,48);
}

.loc-control button {
  width: 40px;
  height: 40px;
  margin-left: 3px;
  border-radius: 50%;
  outline: none;
  background: yellowgreen;
  text-transform: uppercase;
}
